body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.overviewContainer {
  overflow-x: auto;
  display: flex;
}

.svgScroller {
  overflow-x: auto;
}

.column {
  min-width: 300px;
  width: 300px;
  margin-left: 40px;
  margin-right: 40px;
}


.smallStep {
  background: #ddd;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 12px;
}

.svgContainer { 
	z-index: -10;
	position:absolute;
	opacity: 0.5;
}
